import Buslist from '../common/buslist.vue'
import xflSelect from '../common/xfl-select/xfl-select.vue';
export default{
	components:{
		Buslist,xflSelect
	},
	data(){
		return{
			list1:['光谷大道吴家湾','光谷大道金融港','光谷大三李成','光棍大道关南村',"光谷大道国际花园",'光谷大道凌家山','光谷大道现代世贸中心','民族大道光谷广场'],
			selectValue:"",
			lineStrlist:[],
			lineList:[],
			id:null,
			lineId:null,
			content:""
		}
	},
	onLoad(e){
		this.id=e.id
	},
	methods:{
		getLines(){
			this.$http.post("sLine/securityLineQuery",{
				id:this.id
			}).then(res=>{
				if(res.code==100){
					let list=res.info
					list.forEach((item,index)=>{
						this.lineList[index]=item
						this.lineStrlist[item]=item.name
					})
				}
			})
		},
		selectChange(val){
			console.log(val)
			let index=val.index
			this.lineId=this.lineList[index].id
			this.getSite()
		},
		getSite(){
			// 获取线路站点
		},
		isOk(){
			if(!this.content){
				uni.showToast({
					icon:"none",
					title:"请输入消息内容"
				})
			}else if(!this.lineId){
				uni.showToast({
					icon:"none",
					title:"请选择线路"
				})
			}else{
				this.$http.post("sMessage/sendGroupMsg",{
					securityId:this.id,
					lineId:this.lineId,
					content:this.content
				}).then(res=>{
					if(res.code==100){
						uni.showToast({
							icon:"success",
							title:"发送成功！"
						})
					}
				})
			}
			
		}
	}
}
